      return `https://picsum.photos/600/400/?image=${imageId}`;
    },
    onSwiper(swiper) {

    },
    onSlideChange() {
      console.log('slide change');
    },
    // 지역별 추천여행코스 탭 스와이퍼
    onTabSwiper(dataid) {
      // 탭박스활성화
      let tabSwipers = document.querySelectorAll('.swiper-mini .sw');
      for (const tabSwiper of tabSwipers) tabSwiper.classList.remove("on");
      document.querySelector(`#${dataid}`).classList.add("on");
      // 탭버튼 활성화
      let tabButtons = document.querySelectorAll('.tab-ul .tab_btn');
      for (const tabButton of tabButtons) tabButton.classList.remove("on");
      // 내가 누른애 켜라, .tab_btn[data-id='obj-1']
      document.querySelector(`.tab_btn[data-id='${dataid}']`).classList.add("on");

      let tabMaps = document.querySelectorAll('.gangneung-map .gangneung-map-tab');
      for (const tabMap of tabMaps) tabMap.classList.remove("on");
      // 내가 누른애 켜라, .tab_btn[data-id='obj-1']
      document.querySelector(`.gangneung-map-tab[data-id='${dataid}']`).classList.add("on");
    }
  },
